"use client";

import React, { useState, useEffect } from "react";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { Plus, CalendarDays, List } from "lucide-react";
import { NewBookingForm } from "@/components/hotel/NewBookingForm";
import { BookingsList } from "@/components/hotel/BookingsList";

export interface Booking {
    id: string;
    createdAt: number;

    // Guest Details
    guestName: string;
    phone: string;
    email: string;
    address: string;
    city: string;
    state: string;
    pincode: string;

    // ID Verification
    idType: "aadhar" | "pan" | "passport" | "driving_license" | "voter_id";
    idNumber: string;

    // Documents (base64)
    guestPhoto?: string;
    idFrontPhoto?: string;
    idBackPhoto?: string;

    // Stay Details
    checkIn: string;
    checkOut: string;
    roomType: string;
    roomNumber: string;

    // Occupancy
    adults: number;
    children: number;
    extraBeds: number;

    // Purpose
    purpose: string;
    companyName?: string;
    gstNumber?: string;

    // Payment
    totalAmount: number;
    advanceAmount: number;
    paymentMode: "cash" | "card" | "upi" | "bank_transfer";

    // Special Requests
    specialRequests?: string;

    // Status
    status: "confirmed" | "checked_in" | "checked_out" | "cancelled";
}

export default function BookingsPage() {
    const [activeTab, setActiveTab] = useState("all");
    const [bookings, setBookings] = useState<Booking[]>([]);

    useEffect(() => {
        const saved = localStorage.getItem("hotel_bookings");
        if (saved) {
            setBookings(JSON.parse(saved));
        }
    }, []);

    const saveBookings = (newBookings: Booking[]) => {
        setBookings(newBookings);
        localStorage.setItem("hotel_bookings", JSON.stringify(newBookings));
    };

    const handleNewBooking = (booking: Omit<Booking, "id" | "createdAt" | "status">) => {
        const newBooking: Booking = {
            ...booking,
            id: `BK-${Date.now().toString(36).toUpperCase()}`,
            createdAt: Date.now(),
            status: "confirmed",
        };
        saveBookings([newBooking, ...bookings]);
        setActiveTab("all");
    };

    const handleStatusChange = (bookingId: string, newStatus: Booking["status"]) => {
        const updated = bookings.map((b) =>
            b.id === bookingId ? { ...b, status: newStatus } : b
        );
        saveBookings(updated);
    };

    return (
        <div className="space-y-6">
            <div>
                <h1 className="text-2xl font-serif font-semibold text-gray-900 dark:text-white">
                    Bookings
                </h1>
                <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                    Manage guest reservations and check-ins
                </p>
            </div>

            <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
                <TabsList className="bg-white dark:bg-gray-900 border border-gray-200 dark:border-gray-800 p-1 rounded-lg">
                    <TabsTrigger value="all" className="gap-2 data-[state=active]:bg-gray-100 dark:data-[state=active]:bg-gray-800">
                        <List className="h-4 w-4" />
                        All Bookings
                    </TabsTrigger>
                    <TabsTrigger value="new" className="gap-2 data-[state=active]:bg-blue-50 data-[state=active]:text-blue-600 dark:data-[state=active]:bg-blue-950 dark:data-[state=active]:text-blue-400">
                        <Plus className="h-4 w-4" />
                        New Booking
                    </TabsTrigger>
                </TabsList>

                <TabsContent value="all" className="mt-6">
                    <BookingsList
                        bookings={bookings}
                        onStatusChange={handleStatusChange}
                    />
                </TabsContent>

                <TabsContent value="new" className="mt-6">
                    <NewBookingForm onSubmit={handleNewBooking} />
                </TabsContent>
            </Tabs>
        </div>
    );
}
