"use client";

import React, { useState, useRef } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import {
    User,
    Phone,
    Mail,
    MapPin,
    CreditCard,
    Calendar,
    Users,
    Building2,
    FileText,
    Camera,
    Upload,
    Check,
    ChevronRight,
    ChevronLeft,
    BedDouble,
    IndianRupee
} from "lucide-react";
import { cn } from "@/lib/utils";
import type { Booking } from "@/app/dashboard/bookings/page";

interface NewBookingFormProps {
    onSubmit: (booking: Omit<Booking, "id" | "createdAt" | "status">) => void;
}

const STATES = [
    "Andhra Pradesh", "Arunachal Pradesh", "Assam", "Bihar", "Chhattisgarh",
    "Goa", "Gujarat", "Haryana", "Himachal Pradesh", "Jharkhand", "Karnataka",
    "Kerala", "Madhya Pradesh", "Maharashtra", "Manipur", "Meghalaya", "Mizoram",
    "Nagaland", "Odisha", "Punjab", "Rajasthan", "Sikkim", "Tamil Nadu",
    "Telangana", "Tripura", "Uttar Pradesh", "Uttarakhand", "West Bengal",
    "Delhi", "Jammu & Kashmir", "Ladakh"
];

const ID_TYPES = [
    { value: "aadhar", label: "Aadhar Card" },
    { value: "pan", label: "PAN Card" },
    { value: "passport", label: "Passport" },
    { value: "driving_license", label: "Driving License" },
    { value: "voter_id", label: "Voter ID" },
];

const ROOM_TYPES = [
    { value: "standard", label: "Standard Room", price: 2500 },
    { value: "deluxe", label: "Deluxe Room", price: 4000 },
    { value: "suite", label: "Suite", price: 7500 },
    { value: "family", label: "Family Room", price: 5500 },
    { value: "presidential", label: "Presidential Suite", price: 15000 },
];

const PURPOSES = [
    "Leisure / Tourism",
    "Business Trip",
    "Wedding / Event",
    "Medical Visit",
    "Family Visit",
    "Transit / Stopover",
    "Other"
];

export function NewBookingForm({ onSubmit }: NewBookingFormProps) {
    const [step, setStep] = useState(1);
    const fileInputRef = useRef<HTMLInputElement>(null);
    const idFrontRef = useRef<HTMLInputElement>(null);
    const idBackRef = useRef<HTMLInputElement>(null);

    // Form state
    const [formData, setFormData] = useState({
        // Guest Details
        guestName: "",
        phone: "",
        email: "",
        address: "",
        city: "",
        state: "Maharashtra",
        pincode: "",

        // ID Verification
        idType: "aadhar" as Booking["idType"],
        idNumber: "",

        // Documents
        guestPhoto: "",
        idFrontPhoto: "",
        idBackPhoto: "",

        // Stay Details
        checkIn: "",
        checkOut: "",
        roomType: "standard",
        roomNumber: "",

        // Occupancy
        adults: 1,
        children: 0,
        extraBeds: 0,

        // Purpose
        purpose: "Leisure / Tourism",
        companyName: "",
        gstNumber: "",

        // Payment
        totalAmount: 2500,
        advanceAmount: 0,
        paymentMode: "cash" as Booking["paymentMode"],

        // Special Requests
        specialRequests: "",
    });

    const updateField = (field: string, value: any) => {
        setFormData((prev) => {
            const updated = { ...prev, [field]: value };
            // Auto-calculate total if room type changes
            if (field === "roomType") {
                const room = ROOM_TYPES.find((r) => r.value === value);
                if (room) {
                    const nights = calculateNights(prev.checkIn, prev.checkOut);
                    updated.totalAmount = room.price * Math.max(1, nights);
                }
            }
            if (field === "checkIn" || field === "checkOut") {
                const nights = calculateNights(
                    field === "checkIn" ? value : prev.checkIn,
                    field === "checkOut" ? value : prev.checkOut
                );
                const room = ROOM_TYPES.find((r) => r.value === prev.roomType);
                if (room && nights > 0) {
                    updated.totalAmount = room.price * nights;
                }
            }
            return updated;
        });
    };

    const calculateNights = (checkIn: string, checkOut: string) => {
        if (!checkIn || !checkOut) return 1;
        const diff = new Date(checkOut).getTime() - new Date(checkIn).getTime();
        return Math.max(1, Math.ceil(diff / (1000 * 60 * 60 * 24)));
    };

    const handleFileUpload = (field: string, ref: React.RefObject<HTMLInputElement | null>) => {
        const file = ref.current?.files?.[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                updateField(field, reader.result as string);
            };
            reader.readAsDataURL(file);
        }
    };

    const handleSubmit = () => {
        onSubmit(formData);
    };

    const isStep1Valid = formData.guestName && formData.phone && formData.city && formData.state;
    const isStep2Valid = formData.idType && formData.idNumber;
    const isStep3Valid = formData.checkIn && formData.checkOut && formData.roomNumber;
    const isStep4Valid = formData.adults >= 1 && formData.purpose;

    const steps = [
        { num: 1, title: "Guest Details", valid: isStep1Valid },
        { num: 2, title: "ID & Documents", valid: isStep2Valid },
        { num: 3, title: "Stay Details", valid: isStep3Valid },
        { num: 4, title: "Payment", valid: isStep4Valid },
    ];

    return (
        <div className="max-w-4xl mx-auto">
            {/* Progress Steps */}
            <div className="mb-8">
                <div className="flex items-center justify-between relative">
                    <div className="absolute left-0 right-0 top-5 h-0.5 bg-gray-200 dark:bg-gray-800" />
                    <div
                        className="absolute left-0 top-5 h-0.5 bg-blue-500 transition-all duration-300"
                        style={{ width: `${((step - 1) / 3) * 100}%` }}
                    />
                    {steps.map((s) => (
                        <div key={s.num} className="relative z-10 flex flex-col items-center">
                            <button
                                onClick={() => setStep(s.num)}
                                className={cn(
                                    "w-10 h-10 rounded-full flex items-center justify-center text-sm font-medium transition-all",
                                    step === s.num
                                        ? "bg-blue-500 text-white shadow-lg shadow-blue-500/30"
                                        : step > s.num
                                            ? "bg-green-500 text-white"
                                            : "bg-white dark:bg-gray-900 border-2 border-gray-200 dark:border-gray-700 text-gray-400"
                                )}
                            >
                                {step > s.num ? <Check className="h-5 w-5" /> : s.num}
                            </button>
                            <span className={cn(
                                "mt-2 text-xs font-medium",
                                step === s.num ? "text-blue-600 dark:text-blue-400" : "text-gray-500"
                            )}>
                                {s.title}
                            </span>
                        </div>
                    ))}
                </div>
            </div>

            {/* Form Content */}
            <div className="bg-white dark:bg-gray-900 rounded-xl border border-gray-200 dark:border-gray-800 p-6 shadow-sm">
                {/* Step 1: Guest Details */}
                {step === 1 && (
                    <div className="space-y-6">
                        <div className="flex items-center gap-3 pb-4 border-b border-gray-100 dark:border-gray-800">
                            <div className="p-2 bg-blue-50 dark:bg-blue-950 rounded-lg">
                                <User className="h-5 w-5 text-blue-600 dark:text-blue-400" />
                            </div>
                            <div>
                                <h3 className="font-semibold text-gray-900 dark:text-white">Guest Information</h3>
                                <p className="text-sm text-gray-500">Primary guest details</p>
                            </div>
                        </div>

                        <div className="grid gap-5">
                            <div className="grid gap-2">
                                <Label htmlFor="guestName">Full Name *</Label>
                                <Input
                                    id="guestName"
                                    placeholder="Enter guest's full name"
                                    value={formData.guestName}
                                    onChange={(e) => updateField("guestName", e.target.value)}
                                />
                            </div>

                            <div className="grid grid-cols-2 gap-4">
                                <div className="grid gap-2">
                                    <Label htmlFor="phone">Phone Number *</Label>
                                    <div className="relative">
                                        <Phone className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                                        <Input
                                            id="phone"
                                            placeholder="+91 98765 43210"
                                            className="pl-10"
                                            value={formData.phone}
                                            onChange={(e) => updateField("phone", e.target.value)}
                                        />
                                    </div>
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="email">Email Address</Label>
                                    <div className="relative">
                                        <Mail className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                                        <Input
                                            id="email"
                                            type="email"
                                            placeholder="guest@email.com"
                                            className="pl-10"
                                            value={formData.email}
                                            onChange={(e) => updateField("email", e.target.value)}
                                        />
                                    </div>
                                </div>
                            </div>

                            <div className="grid gap-2">
                                <Label htmlFor="address">Address</Label>
                                <div className="relative">
                                    <MapPin className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                                    <Textarea
                                        id="address"
                                        placeholder="Street address, locality"
                                        className="pl-10 min-h-[80px]"
                                        value={formData.address}
                                        onChange={(e) => updateField("address", e.target.value)}
                                    />
                                </div>
                            </div>

                            <div className="grid grid-cols-3 gap-4">
                                <div className="grid gap-2">
                                    <Label htmlFor="city">City *</Label>
                                    <Input
                                        id="city"
                                        placeholder="Mumbai"
                                        value={formData.city}
                                        onChange={(e) => updateField("city", e.target.value)}
                                    />
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="state">State *</Label>
                                    <select
                                        id="state"
                                        className="flex h-10 w-full rounded-md border border-gray-200 bg-white px-3 py-2 text-sm dark:border-gray-800 dark:bg-gray-950 dark:text-gray-100"
                                        value={formData.state}
                                        onChange={(e) => updateField("state", e.target.value)}
                                    >
                                        {STATES.map((s) => (
                                            <option key={s} value={s}>{s}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="pincode">Pincode</Label>
                                    <Input
                                        id="pincode"
                                        placeholder="400001"
                                        value={formData.pincode}
                                        onChange={(e) => updateField("pincode", e.target.value)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                )}

                {/* Step 2: ID & Documents */}
                {step === 2 && (
                    <div className="space-y-6">
                        <div className="flex items-center gap-3 pb-4 border-b border-gray-100 dark:border-gray-800">
                            <div className="p-2 bg-amber-50 dark:bg-amber-950 rounded-lg">
                                <CreditCard className="h-5 w-5 text-amber-600 dark:text-amber-400" />
                            </div>
                            <div>
                                <h3 className="font-semibold text-gray-900 dark:text-white">ID Verification</h3>
                                <p className="text-sm text-gray-500">Government ID and photo documents</p>
                            </div>
                        </div>

                        <div className="grid gap-5">
                            <div className="grid grid-cols-2 gap-4">
                                <div className="grid gap-2">
                                    <Label htmlFor="idType">ID Type *</Label>
                                    <select
                                        id="idType"
                                        className="flex h-10 w-full rounded-md border border-gray-200 bg-white px-3 py-2 text-sm dark:border-gray-800 dark:bg-gray-950 dark:text-gray-100"
                                        value={formData.idType}
                                        onChange={(e) => updateField("idType", e.target.value)}
                                    >
                                        {ID_TYPES.map((t) => (
                                            <option key={t.value} value={t.value}>{t.label}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="idNumber">ID Number *</Label>
                                    <Input
                                        id="idNumber"
                                        placeholder={formData.idType === "aadhar" ? "XXXX XXXX XXXX" : "Enter ID number"}
                                        value={formData.idNumber}
                                        onChange={(e) => updateField("idNumber", e.target.value)}
                                    />
                                </div>
                            </div>

                            {/* Photo Uploads */}
                            <div className="grid grid-cols-3 gap-4 pt-4">
                                {/* Guest Photo */}
                                <div className="space-y-2">
                                    <Label>Guest Photo</Label>
                                    <div
                                        onClick={() => fileInputRef.current?.click()}
                                        className={cn(
                                            "aspect-square rounded-xl border-2 border-dashed flex flex-col items-center justify-center cursor-pointer transition-all hover:border-blue-400 hover:bg-blue-50/50 dark:hover:bg-blue-950/20",
                                            formData.guestPhoto
                                                ? "border-green-400 bg-green-50/50 dark:bg-green-950/20"
                                                : "border-gray-200 dark:border-gray-700"
                                        )}
                                    >
                                        {formData.guestPhoto ? (
                                            <img
                                                src={formData.guestPhoto}
                                                alt="Guest"
                                                className="w-full h-full object-cover rounded-lg"
                                            />
                                        ) : (
                                            <>
                                                <Camera className="h-8 w-8 text-gray-400 mb-2" />
                                                <span className="text-xs text-gray-500">Upload Photo</span>
                                            </>
                                        )}
                                    </div>
                                    <input
                                        ref={fileInputRef}
                                        type="file"
                                        accept="image/*"
                                        className="hidden"
                                        onChange={() => handleFileUpload("guestPhoto", fileInputRef)}
                                    />
                                </div>

                                {/* ID Front */}
                                <div className="space-y-2">
                                    <Label>ID Front</Label>
                                    <div
                                        onClick={() => idFrontRef.current?.click()}
                                        className={cn(
                                            "aspect-square rounded-xl border-2 border-dashed flex flex-col items-center justify-center cursor-pointer transition-all hover:border-blue-400 hover:bg-blue-50/50 dark:hover:bg-blue-950/20",
                                            formData.idFrontPhoto
                                                ? "border-green-400 bg-green-50/50 dark:bg-green-950/20"
                                                : "border-gray-200 dark:border-gray-700"
                                        )}
                                    >
                                        {formData.idFrontPhoto ? (
                                            <img
                                                src={formData.idFrontPhoto}
                                                alt="ID Front"
                                                className="w-full h-full object-cover rounded-lg"
                                            />
                                        ) : (
                                            <>
                                                <FileText className="h-8 w-8 text-gray-400 mb-2" />
                                                <span className="text-xs text-gray-500">ID Front</span>
                                            </>
                                        )}
                                    </div>
                                    <input
                                        ref={idFrontRef}
                                        type="file"
                                        accept="image/*"
                                        className="hidden"
                                        onChange={() => handleFileUpload("idFrontPhoto", idFrontRef)}
                                    />
                                </div>

                                {/* ID Back */}
                                <div className="space-y-2">
                                    <Label>ID Back</Label>
                                    <div
                                        onClick={() => idBackRef.current?.click()}
                                        className={cn(
                                            "aspect-square rounded-xl border-2 border-dashed flex flex-col items-center justify-center cursor-pointer transition-all hover:border-blue-400 hover:bg-blue-50/50 dark:hover:bg-blue-950/20",
                                            formData.idBackPhoto
                                                ? "border-green-400 bg-green-50/50 dark:bg-green-950/20"
                                                : "border-gray-200 dark:border-gray-700"
                                        )}
                                    >
                                        {formData.idBackPhoto ? (
                                            <img
                                                src={formData.idBackPhoto}
                                                alt="ID Back"
                                                className="w-full h-full object-cover rounded-lg"
                                            />
                                        ) : (
                                            <>
                                                <FileText className="h-8 w-8 text-gray-400 mb-2" />
                                                <span className="text-xs text-gray-500">ID Back</span>
                                            </>
                                        )}
                                    </div>
                                    <input
                                        ref={idBackRef}
                                        type="file"
                                        accept="image/*"
                                        className="hidden"
                                        onChange={() => handleFileUpload("idBackPhoto", idBackRef)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                )}

                {/* Step 3: Stay Details */}
                {step === 3 && (
                    <div className="space-y-6">
                        <div className="flex items-center gap-3 pb-4 border-b border-gray-100 dark:border-gray-800">
                            <div className="p-2 bg-purple-50 dark:bg-purple-950 rounded-lg">
                                <BedDouble className="h-5 w-5 text-purple-600 dark:text-purple-400" />
                            </div>
                            <div>
                                <h3 className="font-semibold text-gray-900 dark:text-white">Stay Details</h3>
                                <p className="text-sm text-gray-500">Room and date selection</p>
                            </div>
                        </div>

                        <div className="grid gap-5">
                            <div className="grid grid-cols-2 gap-4">
                                <div className="grid gap-2">
                                    <Label htmlFor="checkIn">Check-in Date *</Label>
                                    <div className="relative">
                                        <Calendar className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                                        <Input
                                            id="checkIn"
                                            type="date"
                                            className="pl-10"
                                            value={formData.checkIn}
                                            onChange={(e) => updateField("checkIn", e.target.value)}
                                        />
                                    </div>
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="checkOut">Check-out Date *</Label>
                                    <div className="relative">
                                        <Calendar className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                                        <Input
                                            id="checkOut"
                                            type="date"
                                            className="pl-10"
                                            value={formData.checkOut}
                                            onChange={(e) => updateField("checkOut", e.target.value)}
                                        />
                                    </div>
                                </div>
                            </div>

                            <div className="grid grid-cols-2 gap-4">
                                <div className="grid gap-2">
                                    <Label htmlFor="roomType">Room Type</Label>
                                    <select
                                        id="roomType"
                                        className="flex h-10 w-full rounded-md border border-gray-200 bg-white px-3 py-2 text-sm dark:border-gray-800 dark:bg-gray-950 dark:text-gray-100"
                                        value={formData.roomType}
                                        onChange={(e) => updateField("roomType", e.target.value)}
                                    >
                                        {ROOM_TYPES.map((r) => (
                                            <option key={r.value} value={r.value}>
                                                {r.label} - ₹{r.price.toLocaleString()}/night
                                            </option>
                                        ))}
                                    </select>
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="roomNumber">Room Number *</Label>
                                    <Input
                                        id="roomNumber"
                                        placeholder="e.g., 301"
                                        value={formData.roomNumber}
                                        onChange={(e) => updateField("roomNumber", e.target.value)}
                                    />
                                </div>
                            </div>

                            <div className="grid grid-cols-3 gap-4">
                                <div className="grid gap-2">
                                    <Label htmlFor="adults">Adults *</Label>
                                    <Input
                                        id="adults"
                                        type="number"
                                        min={1}
                                        max={10}
                                        value={formData.adults}
                                        onChange={(e) => updateField("adults", parseInt(e.target.value) || 1)}
                                    />
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="children">Children</Label>
                                    <Input
                                        id="children"
                                        type="number"
                                        min={0}
                                        max={10}
                                        value={formData.children}
                                        onChange={(e) => updateField("children", parseInt(e.target.value) || 0)}
                                    />
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="extraBeds">Extra Beds</Label>
                                    <Input
                                        id="extraBeds"
                                        type="number"
                                        min={0}
                                        max={3}
                                        value={formData.extraBeds}
                                        onChange={(e) => updateField("extraBeds", parseInt(e.target.value) || 0)}
                                    />
                                </div>
                            </div>

                            <div className="grid grid-cols-2 gap-4">
                                <div className="grid gap-2">
                                    <Label htmlFor="purpose">Purpose of Visit</Label>
                                    <select
                                        id="purpose"
                                        className="flex h-10 w-full rounded-md border border-gray-200 bg-white px-3 py-2 text-sm dark:border-gray-800 dark:bg-gray-950 dark:text-gray-100"
                                        value={formData.purpose}
                                        onChange={(e) => updateField("purpose", e.target.value)}
                                    >
                                        {PURPOSES.map((p) => (
                                            <option key={p} value={p}>{p}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="companyName">Company Name (Optional)</Label>
                                    <div className="relative">
                                        <Building2 className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                                        <Input
                                            id="companyName"
                                            placeholder="For business travelers"
                                            className="pl-10"
                                            value={formData.companyName}
                                            onChange={(e) => updateField("companyName", e.target.value)}
                                        />
                                    </div>
                                </div>
                            </div>

                            {formData.companyName && (
                                <div className="grid gap-2">
                                    <Label htmlFor="gstNumber">GST Number (Optional)</Label>
                                    <Input
                                        id="gstNumber"
                                        placeholder="22AAAAA0000A1Z5"
                                        value={formData.gstNumber}
                                        onChange={(e) => updateField("gstNumber", e.target.value)}
                                    />
                                </div>
                            )}
                        </div>
                    </div>
                )}

                {/* Step 4: Payment */}
                {step === 4 && (
                    <div className="space-y-6">
                        <div className="flex items-center gap-3 pb-4 border-b border-gray-100 dark:border-gray-800">
                            <div className="p-2 bg-green-50 dark:bg-green-950 rounded-lg">
                                <IndianRupee className="h-5 w-5 text-green-600 dark:text-green-400" />
                            </div>
                            <div>
                                <h3 className="font-semibold text-gray-900 dark:text-white">Payment Details</h3>
                                <p className="text-sm text-gray-500">Billing and special requests</p>
                            </div>
                        </div>

                        <div className="grid gap-5">
                            {/* Summary Card */}
                            <div className="p-4 rounded-lg bg-gray-50 dark:bg-gray-800/50 border border-gray-100 dark:border-gray-700">
                                <div className="flex justify-between items-center mb-3">
                                    <span className="text-sm text-gray-600 dark:text-gray-400">Room Type</span>
                                    <span className="font-medium">{ROOM_TYPES.find(r => r.value === formData.roomType)?.label}</span>
                                </div>
                                <div className="flex justify-between items-center mb-3">
                                    <span className="text-sm text-gray-600 dark:text-gray-400">Duration</span>
                                    <span className="font-medium">{calculateNights(formData.checkIn, formData.checkOut)} Night(s)</span>
                                </div>
                                <div className="flex justify-between items-center pt-3 border-t border-gray-200 dark:border-gray-700">
                                    <span className="font-semibold">Total Amount</span>
                                    <span className="text-xl font-bold text-green-600">₹{formData.totalAmount.toLocaleString()}</span>
                                </div>
                            </div>

                            <div className="grid grid-cols-2 gap-4">
                                <div className="grid gap-2">
                                    <Label htmlFor="advanceAmount">Advance Payment</Label>
                                    <div className="relative">
                                        <IndianRupee className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                                        <Input
                                            id="advanceAmount"
                                            type="number"
                                            min={0}
                                            className="pl-10"
                                            value={formData.advanceAmount}
                                            onChange={(e) => updateField("advanceAmount", parseInt(e.target.value) || 0)}
                                        />
                                    </div>
                                </div>
                                <div className="grid gap-2">
                                    <Label htmlFor="paymentMode">Payment Mode</Label>
                                    <select
                                        id="paymentMode"
                                        className="flex h-10 w-full rounded-md border border-gray-200 bg-white px-3 py-2 text-sm dark:border-gray-800 dark:bg-gray-950 dark:text-gray-100"
                                        value={formData.paymentMode}
                                        onChange={(e) => updateField("paymentMode", e.target.value)}
                                    >
                                        <option value="cash">Cash</option>
                                        <option value="card">Card</option>
                                        <option value="upi">UPI</option>
                                        <option value="bank_transfer">Bank Transfer</option>
                                    </select>
                                </div>
                            </div>

                            <div className="grid gap-2">
                                <Label htmlFor="specialRequests">Special Requests</Label>
                                <Textarea
                                    id="specialRequests"
                                    placeholder="Any special requirements, preferences, or notes..."
                                    className="min-h-[100px]"
                                    value={formData.specialRequests}
                                    onChange={(e) => updateField("specialRequests", e.target.value)}
                                />
                            </div>
                        </div>
                    </div>
                )}

                {/* Navigation Buttons */}
                <div className="flex justify-between mt-8 pt-6 border-t border-gray-100 dark:border-gray-800">
                    <Button
                        variant="outline"
                        onClick={() => setStep(Math.max(1, step - 1))}
                        disabled={step === 1}
                        className="gap-2"
                    >
                        <ChevronLeft className="h-4 w-4" />
                        Previous
                    </Button>

                    {step < 4 ? (
                        <Button
                            onClick={() => setStep(Math.min(4, step + 1))}
                            className="gap-2 bg-blue-600 hover:bg-blue-700"
                        >
                            Next
                            <ChevronRight className="h-4 w-4" />
                        </Button>
                    ) : (
                        <Button
                            onClick={handleSubmit}
                            className="gap-2 bg-green-600 hover:bg-green-700"
                        >
                            <Check className="h-4 w-4" />
                            Create Booking
                        </Button>
                    )}
                </div>
            </div>
        </div>
    );
}
